:root {
    --funkyGreen: rgb(122, 248, 79);
  }
  
  .cards {
      padding: 0rem;
      background: transparent;
    }

    
    
    .cards__container {
      
      flex-flow: column;
      align-items: center;
      max-width: 60vw;
      margin: 0 auto;
  
  
    }
    
    .cards__wrapper {
      position: relative;
      margin: 2vw 0 1vw;
  
  
    }
    
    .cards__items {
      margin-bottom: 24px;
    }
  
    .cards__item {
      display: flex;
      flex: 1;
      margin: 0 1rem;
      border-radius: 10px;
      aspect-ratio: 14/5;

      border-color: var(--funkyGreen);
      border-style: solid;
      align-items: center;
      justify-content: center;
      text-align: center;
      background-color: black;
  
    }

    .cards__item__title_text {
        color: var(--funkyGreen);
      font-size: 2.5vh;
      line-height: 32px;
      padding-top: 15px;
    }

    .clicksection {
        z-index: 99;
        flex: 1;
        margin: 0 0;
        border-radius: 10px;
  
      }

    

    
  
    
    .cards__item__link {
      display: flex;
      flex-flow: column;
      width: 100%;
  
      border-radius: 10px;
      overflow: hidden;
      text-decoration: none;
    }
    
    .cards__item__pic-wrap {
      position: relative;
      width: 100%;
      padding-top: 67%;
      overflow: hidden;
    }
    
    .fade-img {
      animation-name: fade-img;
      animation-duration: 2s;
    }
    
    .cards__item__pic-wrap::after {
      content: attr(data-category);
      position: absolute;
      bottom: 0;
      margin-left: 10px;
      padding: 6px 8px;
      max-width: calc((100%) - 60px);
      font-size: 12px;
      font-weight: 700;
      color: #fff;
      background-color: #1f98f4;
      box-sizing: border-box;
    }
    
    .cards__item__img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      max-width: 100%;
      height: 100%;
      max-height: 100%;
      object-fit: cover;
      transition: all 0.2s linear;
    }
    
    
    
    .cards__item__info {
      padding: 20px 30px 30px;
    }
    
    .cards__item__text {
      color: var(--funkyGreen);
      font-size: 2.2vh;
      line-height: 24px;
      list-style: none;
    }
    
    @media only screen and (min-width: 1200px) {
      .content__blog__container {
        width: 84%;
      }
    }
    
    @media only screen and (min-width: 1024px) {
      .cards__items {
        display: flex;
      }
    }
    
    @media only screen and (max-width: 1024px) {
      .cards__item {
        margin: 2vh;
        
      }
      .cardsnstuff h1 {
        font-size: 15vw;
      }
      .cards__container {
        max-width: 90vw;
      }
  

    }
  
    .cardsnstuff {
      height: 100vh;
      width: 100%;
  
      justify-content: center;
      align-items: center;
  
      position: fixed;
  
  
      
  }
  
  .cardsnstuff h1 {
    font-size: calc(5vw + 35px);
      color: rgb(122, 248, 79);
      margin-top: 3vh;
  }